
































import Vue from 'vue';
import { Component, Prop } from 'vue-property-decorator';

@Component({})
export default class ReplyToComment extends Vue {
  @Prop({default: false}) isReplyToCommentVisible!: boolean;

  closeReply() {
    this.$emit('update:isReplyToCommentVisible', false);
  }

  reply() {
    this.closeReply();
  }
}

